import { listArrearRecords as listArrearRecordsFromApi, Confirm as ConfirmFromApi, payBack as paybackApi } from 'api/arrear.js';

function startLoading() {
    return {
        type: '@ARREAR/START_LOADING'
    };
};
function endListRecords(arrearRecords) {
    return {
        type: '@ARREAR/END_LIST_RECORDS',
        arrearRecords: arrearRecords
    };
};
function endLoading() {
    return {
        type: '@ARREAR/END_LOADING'
    };
};

export function noticePayBack(id, payback) {
    return function (dispatch, getState) {
        return paybackApi(id, payback).then(function () {
            dispatch(listArrearRecords(localStorage.getItem('Account')));
        }).catch(function (err) {
            console.error('Error listing arrearRecords', err);
        });
    };
}
export function Confirm(id) {
    return function (dispatch, getState) {
        dispatch(startLoading());
        console.log('in confirm');
        return ConfirmFromApi(id).then(function () {}).catch(function (err) {
            console.error('Error listing arrearRecords', err);
        }).then(function () {
            dispatch(listArrearRecords(localStorage.getItem('Account')));
            dispatch(endLoading());
        });
    };
};
export function listArrearRecords(user_account) {
    return function (dispatch, getState) {
        dispatch(startLoading());
        console.log('in list record');
        var target_account = localStorage.getItem('friend_account');
        if (target_account === null) target_account = 'unknown';
        return listArrearRecordsFromApi(localStorage.getItem('Account'), target_account).then(function (arrearRecords) {
            dispatch(endListRecords(arrearRecords));
        }).catch(function (err) {
            console.error('Error listing arrearRecords', err);
        }).then(function () {
            console.log('finish list');
            dispatch(endLoading());
        });
    };
};


